<template>
	<view>
		<view class="page-container">
			<view class="info-card">
				<view class="title-container">
					<text class="title" v-if="!isEditing">{{grassInfo.type}}</text>
					<u-input class="title-input" v-else v-model="editInfo.type" placeholder="请输入牧草名称" border="surround"
						@change="change"></u-input>
					<!-- <view class="tag" @click="isEditing ? showGradeSelect() : ''">
						<text v-if="!isEditing">{{getNutritionValue()}}</text>
						<text v-else class="tag-select">{{editInfo.grade || '选择等级'}}</text>
					</view> -->
				</view>
				<view class="" v-for="(item,index) in grassInfo.settingContent" :key="index">
					<view class="form-item" v-if="item.type == 'input'">
						<view class="item-label">{{item.name}}</view>
						<u-input class="item-input" v-model="item.value" :placeholder="'请填写'+item.name"
							:border="false" :disabled="!isEditing"></u-input>
					</view>
					<view class="form-item radio-item" v-if="item.type == 'radio'">
						<view class="item-label">{{item.name}}</view>
						<view class="radio-group" v-if="isEditing">
							<u-radio-group v-model="item.value" placement="row">
								<u-radio v-for="(data, indexs) in item.options" :key="indexs" :name="data.value"
									:label="data.value" active-color="#65ad66" @change="radioChange">
									{{data.value}}
								</u-radio>
							</u-radio-group>
						</view>
						<view class="value-text" v-else>{{item.value}}</view>
					</view>
				</view>
				
				<view class="form-item">
					<view class="item-label">地理位置</view>
					<u-input class="item-input" v-if="isEditing" v-model="grassInfo.addressNames" placeholder="省市区" :border="false" disabled
						@click="chooseAddress"></u-input>
					<view class="value-text" v-else>{{grassInfo.addressNames}}</view>
				</view>
			</view>

			<view class="section">
				<v-title text="照片"></v-title>
				<view class="image-grid">
					<u-grid :col="3" :border="false">
						<u-grid-item v-for="(item, index) in isEditing ? editInfo.images : images" :key="index">
							<v-image :src="httpUrl+item" width="220" height="160" :border-radius="10"
								@click="isEditing ? chooseImage(index) : previewImage(item, images)"></v-image>
							<view class="delete-btn" v-if="isEditing" @click.stop="deleteImage(index)">
								<u-icon name="close" color="#fff" size="20"></u-icon>
							</view>
						</u-grid-item>
						<u-grid-item v-if="isEditing && editInfo.images.length < 9">
							<view class="add-image" @click="chooseImage(-1)">
								<u-icon name="plus" color="#999" size="40"></u-icon>
							</view>
						</u-grid-item>
					</u-grid>
				</view>
			</view>

			<view class="section">
				<v-title text="视频"></v-title>
				<view class="video-item">
					<video :src="httpUrl + (isEditing ? editInfo.videoUrl : videoUrl)" controls show-center-play-btn
						show-play-btn initial-time="0" class="video-player" poster="/static/imgCs/1.png"></video>
					<view class="edit-video-btn" v-if="isEditing" @click="chooseVideo">
						<u-icon name="reload" color="#fff" size="24"></u-icon>
						<text class="edit-video-text">更换视频</text>
					</view>
				</view>
			</view>

			<!-- 添加占位区域，防止内容被底部footer遮挡 -->
			<view class="footer-placeholder"></view>

			<view class="footer" v-if="role === 'pasture'">
				<button class="btn" :class="isEditing ? 'btn-cancel' : 'btn-edit'"
					@click="isEditing ? cancelEdit() : startEdit()">
					{{isEditing ? '取消' : '修改'}}
				</button>
				<button class="btn" :class="isEditing ? 'btn-save' : ''"
					@click="isEditing ? saveEdit() : ''" v-if="isEditing">
					{{isEditing ? '保存' : ''}}
				</button>
			</view>

			<!-- 省市区选择器 -->
			<u-picker mode="region" v-model="showRegionPicker" :default-region="defaultRegion"
				@confirm="onRegionConfirm"></u-picker>
			<!-- 添加等级选择器 -->
			<u-select v-model="showGradeSelector" :list="gradeOptions" @confirm="confirmGrade"></u-select>

			<!-- 添加茬数选择器 -->
			<u-select v-model="showCutSelector" :list="cutOptions" @confirm="confirmCut"></u-select>
		</view>
	</view>
</template>

<script>
	import {
		userApi
	} from '../../../api/user.js';
	import settings from '../../../common/settings.js';
	export default {
		data() {
			return {
				dataContent: {},
				httpUrl: "",
				id: "",
				isEditing: false,
				videoUrl: "",
				// 图片数组
				images: [],
				grassInfo: {
					type: '',
					addressNames: '',
					settingContent: []
				},
				editInfo: {
					name: '',
					grade: '',
					location: '',
					type: '',
					weight: '',
					ash: '',
					water: '',
					images: [],
					videoUrl: ''
				},
				// 新增等级选择器相关数据
				showGradeSelector: false,
				gradeOptions: [
					// 将根据API返回的数据动态生成
				],
				// 茬数选择器
				showCutSelector: false,
				cutOptions: [
					// 将根据API返回的数据动态生成
				],
				showRegionPicker: false,
				defaultRegion: ['北京市', '北京市', '东城区'],
				region: {
					province: '',
					city: '',
					area: ''
				},
				role:""
			};
		},
		onLoad(options) {
			const userInfo = uni.getStorageSync('userInfo');
			this.role = userInfo.user.roles[0].roleKey
			this.httpUrl = settings.devUrl
			console.log(options);
			this.id = options.id
			this.getList();
		},
		methods: {
			// 接口请求
			getList() {
				// 详情接口
				userApi.pastureList(this.id).then(res => {
					console.log(res)
					this.dataContent = res.data
					this.grassInfo = res.data

					// 处理图片列表
					if (res.data.picUrlList) {
						this.images = res.data.picUrlList.split(',')
					}

					// 处理视频URL
					this.videoUrl = res.data.videoUrl || ""

					// 如果settingContent是字符串，转换为对象
					try {
						if (typeof res.data.settingContent === 'string') {
							this.grassInfo.settingContent = JSON.parse(res.data.settingContent)
						} else {
							this.grassInfo.settingContent = res.data.settingContent || []
						}

						// 设置等级选择器的选项
						this.setGradeOptions()
						// 设置茬数选择器的选项
						this.setCutOptions()
					} catch (e) {
						console.error('解析settingContent失败', e)
						this.grassInfo.settingContent = []
					}
				})
			},
			// 显示省市区选择器
			chooseAddress() {
				if (this.isEditing) {
					this.showRegionPicker = true;
				}
			},
			onRegionConfirm(e) {
				this.region.province = e.province.label;
				this.region.city = e.city.label;
				this.region.area = e.area.label;

				// 格式化显示省市区
				const formattedAddress = `${this.region.province} ${this.region.city} ${this.region.area}`;
				this.formData.addressNames = formattedAddress;

				// 如果有省市区的code值，可以设置真实的addressIds
				if (e.province.value && e.city.value && e.area.value) {
					// 使用真实的区域代码作为addressIds
					const addressCodes = `${e.province.value},${e.city.value},${e.area.value}`;
					console.log('地区代码:', addressCodes);
					this.formData.addressIds = addressCodes;
					// 如果后端需要区域代码而不是名称，可以取消下面的注释
					// this.formData.addressIds = addressCodes;
				}

				// 关闭选择器
				this.showRegionPicker = false;
			},
			// 设置等级选择器的选项
			setGradeOptions() {
				const nutrition = this.grassInfo.settingContent.find(item => item.name === '营养价值RFV')
				if (nutrition && nutrition.options && nutrition.options.length > 0) {
					this.gradeOptions = nutrition.options.map((opt, index) => {
						return {
							value: String(index + 1),
							label: opt.value
						}
					})
				} else {
					// 默认选项
					this.gradeOptions = [{
							value: '1',
							label: '指标185'
						},
						{
							value: '2',
							label: '指标170-185'
						},
						{
							value: '3',
							label: '指标150-170'
						}
					]
				}
			},
			// 设置茬数选择器的选项
			setCutOptions() {
				const cutItem = this.grassInfo.settingContent.find(item => item.name === '茬数')
				if (cutItem && cutItem.options && cutItem.options.length > 0) {
					this.cutOptions = cutItem.options.map((opt, index) => {
						return {
							value: String(index + 1),
							label: opt.value
						}
					})
				} else {
					// 默认选项
					this.cutOptions = [{
							value: '1',
							label: '一茬'
						},
						{
							value: '2',
							label: '二茬'
						},
						{
							value: '3',
							label: '三茬'
						},
						{
							value: '4',
							label: '四茬'
						}
					]
				}
			},
			// 获取营养价值
			getNutritionValue() {
				if (this.grassInfo.settingContent && this.grassInfo.settingContent.length > 0) {
					const nutrition = this.grassInfo.settingContent.find(item => item.name === '营养价值RFV')
					return nutrition ? nutrition.value : ''
				}
				return ''
			},
			// 获取茬数
			getCutNumber() {
				if (this.grassInfo.settingContent && this.grassInfo.settingContent.length > 0) {
					const cut = this.grassInfo.settingContent.find(item => item.name === '茬数')
					return cut ? cut.value : ''
				}
				return ''
			},
			// 获取总重量
			getTotalWeight() {
				if (this.grassInfo.settingContent && this.grassInfo.settingContent.length > 0) {
					const weight = this.grassInfo.settingContent.find(item => item.name === '总量/吨')
					return weight ? weight.value + '吨' : ''
				}
				return ''
			},
			// 获取水分
			getWaterContent() {
				if (this.grassInfo.settingContent && this.grassInfo.settingContent.length > 0) {
					const water = this.grassInfo.settingContent.find(item => item.name === '水分')
					return water ? water.value : ''
				}
				return ''
			},
			previewImage(current, urls) {
				// 创建完整的URL数组（添加域名前缀，如果是网络图片）
				let imageUrls = [];

				// 处理相对路径图片，确保在小程序中能正确显示
				if (urls && urls.length) {
					urls.forEach(item => {
						// 判断是否为网络图片
						if (item.startsWith('http')) {
							imageUrls.push(item);
						} else {
							// 对于本地图片，需要使用绝对路径
							const imgPath = this.httpUrl + item;
							imageUrls.push(imgPath);
						}
					});
				} else {
					// 如果只有一张图片
					if (current.startsWith('http')) {
						imageUrls = [current];
					} else {
						imageUrls = [this.httpUrl + current];
					}
				}

				console.log('预览图片', current, imageUrls);

				// 调用预览API
				uni.previewImage({
					current: current.startsWith('http') ? current : this.httpUrl + current,
					urls: imageUrls,
					success: () => {
						console.log('预览图片成功');
					},
					fail: (err) => {
						console.error('预览图片失败', err);
						uni.showToast({
							title: '图片预览失败',
							icon: 'none'
						});
					}
				});
			},
			// 添加 change 方法处理输入变化
			change(value) {
				console.log('输入值变化：', value);
			},
			startEdit() {
				// 进入编辑模式
				this.isEditing = true;
				// 复制当前数据到编辑对象
				this.editInfo = {
					name: this.grassInfo.name,
					grade: this.getNutritionValue(),
					location: this.grassInfo.addressNames,
					type: this.grassInfo.type,
					weight: this.getTotalWeight(),
					ash: this.getCutNumber(),
					water: this.getWaterContent(),
					images: [...this.images],
					videoUrl: this.videoUrl
				};
			},
			cancelEdit() {
				// 退出编辑模式
				this.isEditing = false;
				uni.showToast({
					title: '已取消编辑',
					icon: 'none'
				});
			},
			saveEdit() {
				// 准备要提交的数据
				const params = {
					id: this.grassInfo.id, // 确保包含修改详情的id
					type: this.editInfo.type,
					// 地址相关信息保持原样
					addressIds: this.grassInfo.addressIds,
					addressNames: this.grassInfo.addressNames,
					// 如果更新了地址信息
					...(this.formData?.addressIds && { addressIds: this.formData.addressIds }),
					...(this.formData?.addressNames && { addressNames: this.formData.addressNames }),
					// 图片和视频相关
					picUrlList: this.editInfo.images.join(','),
					videoUrl: this.editInfo.videoUrl,
					// 设置内容 - 直接使用当前表单的动态数据
					settingContent: JSON.stringify(this.grassInfo.settingContent),
					// 其他必要字段
					number: this.grassInfo.number,
					farmHouseholdId: this.grassInfo.farmHouseholdId
				};

				console.log('提交的修改数据:', params);
				
				// 显示加载中状态
				uni.showLoading({
					title: '保存中...'
				});
				
				console.log(params.settingContent)
				// 调用修改接口保存数据
				userApi.edit(params).then(res => {
					if (res.code === 200) {
						uni.showToast({
							title: '保存成功',
							icon: 'success'
						});
						// 重新加载数据
						this.getList();
					} else {
						uni.showToast({
							title: res.msg || '保存失败',
							icon: 'none'
						});
					}
					// 退出编辑模式
					this.isEditing = false;
				}).catch(err => {
					console.error('更新失败', err);
					uni.showToast({
						title: '保存失败，请重试',
						icon: 'none'
					});
					// 退出编辑模式
					this.isEditing = false;
				}).finally(() => {
					uni.hideLoading();
				});
			},
			chooseImage(index) {
				uni.chooseImage({
					count: 1,
					sizeType: ['compressed'],
					sourceType: ['album', 'camera'],
					success: (res) => {
						const tempFilePaths = res.tempFilePaths;

						// 实际应用中，这里应该上传图片到服务器
						this.uploadImage(tempFilePaths[0], index);
					}
				});
			},
			// 上传图片到服务器
			uploadImage(filePath, index) {
				uni.showLoading({
					title: '上传中...'
				});

				uni.uploadFile({
					url: this.httpUrl + '/api/common/upload', // 替换为实际的上传API
					filePath: filePath,
					name: 'file',
					formData: {
						'module': 'pasture' // 根据实际需求设置上传模块
					},
					success: (uploadRes) => {
						uni.hideLoading();

						// 解析返回的JSON数据
						try {
							const data = JSON.parse(uploadRes.data);
							if (data.code === 200) {
								const url = data.url || data.data.url;

								if (index === -1) {
									// 添加新图片
									this.editInfo.images.push(url);
								} else {
									// 替换已有图片
									this.$set(this.editInfo.images, index, url);
								}

								uni.showToast({
									title: '上传成功',
									icon: 'success'
								});
							} else {
								uni.showToast({
									title: data.msg || '上传失败',
									icon: 'none'
								});
							}
						} catch (e) {
							console.error('解析上传结果失败', e);
							uni.showToast({
								title: '上传失败',
								icon: 'none'
							});
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							title: '上传失败',
							icon: 'none'
						});
					}
				});
			},
			deleteImage(index) {
				uni.showModal({
					title: '提示',
					content: '确定要删除这张图片吗？',
					success: (res) => {
						if (res.confirm) {
							this.editInfo.images.splice(index, 1);
						}
					}
				});
			},
			chooseVideo() {
				uni.chooseVideo({
					sourceType: ['album', 'camera'],
					maxDuration: 60,
					camera: 'back',
					success: (res) => {
						// 实际应用中，这里应该上传视频到服务器
						this.uploadVideo(res.tempFilePath);
					}
				});
			},
			// 上传视频到服务器
			uploadVideo(filePath) {
				uni.showLoading({
					title: '上传中...'
				});

				uni.uploadFile({
					url: this.httpUrl + '/api/common/upload', // 替换为实际的上传API
					filePath: filePath,
					name: 'file',
					formData: {
						'module': 'pasture' // 根据实际需求设置上传模块
					},
					success: (uploadRes) => {
						uni.hideLoading();

						// 解析返回的JSON数据
						try {
							const data = JSON.parse(uploadRes.data);
							if (data.code === 200) {
								this.editInfo.videoUrl = data.url || data.data.url;

								uni.showToast({
									title: '上传成功',
									icon: 'success'
								});
							} else {
								uni.showToast({
									title: data.msg || '上传失败',
									icon: 'none'
								});
							}
						} catch (e) {
							console.error('解析上传结果失败', e);
							uni.showToast({
								title: '上传失败',
								icon: 'none'
							});
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							title: '上传失败',
							icon: 'none'
						});
					}
				});
			},
			getGrassDetails(id) {
				// 从API获取详情数据
				// 示例
				/*
				this.$api.getGrassDetails(id).then(res => {
					if (res.code === 200) {
						this.grassInfo = res.data;
						this.images = res.data.images || [];
						this.videoUrl = res.data.videoUrl || '';
					}
				});
				*/
			},
			updateGrassDetails() {
				// 调用API更新详情数据
				// 示例
				/*
				const params = {
					id: this.grassInfo.id,
					...this.grassInfo,
					images: this.images,
					videoUrl: this.videoUrl
				};
				
				this.$api.updateGrassDetails(params).then(res => {
					if (res.code === 200) {
						uni.showToast({
							title: '保存成功',
							icon: 'success'
						});
					}
				});
				*/
			},

			// 显示等级选择器
			showGradeSelect() {
				if (this.isEditing) {
					this.showGradeSelector = true;
				}
			},

			// 确认选择等级
			confirmGrade(e) {
				this.editInfo.grade = e[0].label;
			},

			// 显示茬数选择器
			showCutSelect() {
				if (this.isEditing) {
					this.showCutSelector = true;
				}
			},

			// 确认选择茬数
			confirmCut(e) {
				this.editInfo.ash = e[0].label;
			},
			// 处理单选框变化
			radioChange(e) {
				console.log('单选框变化：', e);
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f5f6f9;
	}
.form-item {
		display: flex;
		align-items: center;
		padding: 10rpx 0;
		border-bottom: 2rpx solid #f5f6f9;
		
		.value-text {
			flex: 1;
			font-size: 28rpx;
			color: #333;
		}
	}
	/* 单选按钮样式 */
	.radio-item {
		align-items: flex-start;
		padding: 15rpx 0;
	}
	.radio-group {
		flex: 1;
	}
	
	::v-deep .u-radio-group {
		justify-content: flex-end;
		flex-wrap: wrap;
	}
	
	::v-deep .u-radio {
		margin-left: 20rpx;
		margin-bottom: 10rpx;
	}
	
	::v-deep .u-radio__label {
		font-size: 28rpx;
		color: #606266;
	}
	.item-label {
		width: 160rpx;
		font-size: 28rpx;
		color: #333;
	}
	.item-input {
		flex: 1;
		text-align: right;
	}
	::v-deep .u-grid-item-box {
		padding: 0 !important;

		image {
			border-radius: 15rpx;
		}
	}

	.page-container {
		padding: 20rpx 30rpx;
	}

	.title-container {
		display: flex;
		align-items: center;
		margin-bottom: 30rpx;
